import * as React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"
import BaseChip from '@mui/material/Avatar';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import { styled } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import useMediaQuery from '@mui/material/useMediaQuery';


const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&::before': {
    display: 'none',
  },
}));

const AccordionSummary = styled((props) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row-reverse',
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(90deg)',
  },
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));


const normalizeNumber = (num) => {
  const addUp = `${num}`.split('').reduce((acum, next) => {
    return acum + Number(next)
  }, 0)
  if (`${addUp}`.length === 1) {
    return addUp
  }
  return normalizeNumber(addUp)
}

const checkNumber = (num) => {
  const colorNumberMap = {
    1: {
      color: 'metal',
      element: 'metal'
    },
    2: { color: 'blue', element: 'water' },
    3: { color: 'red', element: 'fire' },
    4: { color: 'green', element: 'wood' },
    5: { color: '#cccc00', element: 'earth' },
    6: { color: 'blue', element: 'water' },
    7: { color: 'metal', element: 'metal' },
    8: { color: 'red', element: 'fire' },
    9: { color: 'green', element: 'wood' },
  }
  return colorNumberMap[num] || 'black'
}

const Chip = ({ children, num }) => {

  return <BaseChip sx={{ maxWidth: 56, maxHeight: 56, bgcolor: checkNumber(num).color }}> {children} </BaseChip>
}


function Seo({ description, lang, meta, title, birthday, onNumber }) {

  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };
  const { day, month, year } = birthday;
  const dateString = `${day}${month}${year}`
  const [a, b, c, d, e, f, g, h] = dateString.split('')
  const [i, j, k, l] = [`${a}${b}`, `${c}${d}`, `${e}${f}`, `${g}${h}`].map(e => normalizeNumber(e))
  const [m, n] = [`${i}${j}`, `${k}${l}`].map(e => normalizeNumber(e))
  const [o] = [`${m}${n}`].map(e => normalizeNumber(e))
  const [q, p] = [`${m}${o}`, `${m}${o}`].map(e => normalizeNumber(e))
  const [r] = [`${q}${p}`].map(e => normalizeNumber(e))
  const [t, s] = [`${i}${m}`, `${j}${m}`].map(e => normalizeNumber(e))
  const u = [`${t}${s}`].map(e => normalizeNumber(e))
  const [v, w] = [`${k}${n}`, `${l}${n}`].map(e => normalizeNumber(e))
  const x = [`${v}${w}`].map(e => normalizeNumber(e))

  const numOfElement = [i, j, k, l, m, n].reduce((acum, next) => {
    const number = checkNumber(next)
    acum[number.element] = acum[number.element] + 1;
    return acum
  }, {
    water: 0,
    earth: 0,
    metal: 0,
    fire: 0,
    wood: 0
  })
  return (
    <Box margin="16px">

      <Box sx={{ p: 2 }}>
        <Stack direction="row" justifyContent="center" spacing={3}>
          {
            [r].map(e => <Chip num={e}>{e}</Chip>)
          }

        </Stack>

        <Stack direction="row" justifyContent="center" spacing={3}>

          {
            [q, p].map(e => <Chip num={e}>{e}</Chip>)
          }

        </Stack>

      </Box>
      <Box display="flex" justifyContent="center" alignItems="center">
        <Stack direction="row" spacing={3} sx={{ paddingRight: '8px' }}>

          {
            [u, t, s].map(e => <Chip num={e}>{e}</Chip>)
          }

        </Stack>
        <Stack direction="row" spacing={3} sx={{ paddingLeft: '8px' }}>

          {
            [v, w, x].map(e => <Chip num={e}>{e}</Chip>)
          }

        </Stack>
      </Box>
      <Box sx={{ p: 2 }}>
        <Box display="flex" alignItems="center" justifyContent="center">
          
        </Box>
        <Stack direction="row" justifyContent="center" spacing={3}>
          {
            [o].map(e => <Chip num={e}>{e}</Chip>)
          }


        </Stack>
        <Stack direction="row" justifyContent="center" spacing={3}>
          {
            [m, n].map(e => <Chip num={e}>{e}</Chip>)
          }
        </Stack>

        <Stack direction="row" justifyContent="center" spacing={3}>
          {
            [i, j, "", k, l].map(e => <Chip num={e}> {e}</Chip>)
          }
        </Stack>
      </Box>
      <Stack direction="row" justifyContent="center" spacing={3}>
        {
          //[a, b, c, d, e, f, g, h].map(e => <Chip >{e}</Chip>)
        }
      </Stack>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            {
              Object.keys(numOfElement).map(k => <Box> {k}: {numOfElement[k]} </Box>)

            }
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
        <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
          <Typography></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <a href="https://api.whatsapp.com/send?phone=6587413159&amp;text=" style={{ color: "rgb(37, 211, 102)" }}>Whatsapp</a> 
          </Typography>
        </AccordionDetails>
      </Accordion>
    </Box>
  )
}

Seo.defaultProps = {
  lang: `en`,
  meta: [],
  description: ``,
}

Seo.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
}

export default Seo
